import React, { Component } from 'react';
import Octave from './components/octave';
import KeySelector from './components/keySelector';
import './App.css';
import Writeup from './components/writeup';

const cMajor = [1,3,5,6,8,10,12];

class App extends Component {

  constructor(props) {
    super();
    this.state = {
      selectedOffsets: cMajor,
      keyName:'c-std'
    };

    this.handleKeyChanged = this.handleKeyChanged.bind(this);
  }

  handleKeyChanged(selectedKeyOffsets, keyName, evt) {
    console.log('app registered the key changed');
    this.setState(prevState => ({ selectedOffsets: selectedKeyOffsets, keyName: keyName }));
  }

  render() {
    return (
      <div className={`App ${this.state.keyName}`}>
        <table>
          <thead>
          <tr>
            <th className='header note-c'>C </th>
            <th className='header note-c-sharp'>C#</th>
            <th className='header note-d'>D</th>
            <th className='header note-d-sharp'>D#</th>
            <th className='header note-e'>E</th>
            <th className='header note-f'>F</th>
            <th className='header note-f-sharp'>F#</th>
            <th className='header note-g'>G</th>
            <th className='header note-g-sharp'>G#</th>
            <th className='header note-a'>A</th>
            <th className='header note-a-sharp'>A#</th>
            <th className='header note-b'>B</th>
          </tr>
          </thead>
          <tbody>
            <tr><td colSpan='12'><KeySelector onKeyChange={this.handleKeyChanged} ></KeySelector></td></tr>
            <Octave level="1" selectedKeyOffsets={this.state.selectedOffsets} />
            <Octave level="2" selectedKeyOffsets={this.state.selectedOffsets} />
            <Octave level="3" selectedKeyOffsets={this.state.selectedOffsets} />
            <Octave level="4" selectedKeyOffsets={this.state.selectedOffsets} />
            <Octave level="5" selectedKeyOffsets={this.state.selectedOffsets} />
            <Octave level="6" selectedKeyOffsets={this.state.selectedOffsets} />
            <Octave level="7" selectedKeyOffsets={this.state.selectedOffsets} />
          </tbody>
        </table>
        <Writeup />
      </div>
    );
  }
}

export default App;
